export default {
  namespaced: true,
  state: {
    profile: null,
    token: null,
    answer: 'expect'
  },
  mutations: {
    SET_PROFILE(state, payload) {
      state.profile = payload;
    },
    SET_TOKEN(state, payload) {
      state.token = payload;
    },
    SET_ANSWER(state, payload) {
      state.answer = payload;
    },
  },
  actions: {
    fetchRegistration ({ commit, getters }) {
      commit('SET_ANSWER', 'expect')
      fetch(process.env.VUE_APP_DOMAIN, {
        method: 'POST',
        body: JSON.stringify(getters.GET_TRIPS),
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        return response.json();
      }).then((data) => {
        console.log(data);
        commit('SET_ANSWER', data)
      });
    },

  },
  getters: {
    GET_PROFILE: (state) => {
      return state.parents;
    },
    GET_TOKEN: (state) => {
      return state.token;
    },
    GET_ANSWER: (state) => {
      return state.answer;
    },
  }
};